/* ////////////////CONTAINERS////////////////////////*/

.hikes-container {
  padding: 30px 3px 3px;
  background-size: cover;
  min-height: 100vh;
}

.hike-page-header {
  color: grey;
}

.favorites-container {
  padding-top: 20px;
  margin: 0 auto;
  width: 200px;
}

.favorites-container h4 {
  text-align: left;
  padding-left: 10%;
}


.favorites-list {
  text-align: justify;
  margin: 0 auto;
  width: 500px;
  padding-left:9%;
}

/* ////////////////////////ADD HIKE TO FAVORITE/////////////////////// */

.fav-btn {
  position: absolute;
  top: 10%;
  left: 40px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  background-color: black;
  opacity: 0.5;
  color: white;
  font-size: 16px;
  padding: 12px 24px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

.fav-btn i:hover {
  color: red;
}

.fav-btn i:active {
  color: red;
}


/* ///////////////////////////////DELETE HIKE FROM FAVORITES ////////////////////////// */

.del-btn {
  padding: 0.5;
  border: none;
  background: none;
}

#del-btn {
  color: silver;
  font-size: 11px;
}

img.profile-pic {
  width: 175px;
  height: 175px;
  border-radius: 50%;
  margin-bottom: 15px;
}

.profile-container {
  margin-top: 60px;
  margin-bottom: 25px;
}

.user-data {
  margin-top: 15px;
}

.username {
  font-size: 35px;
  font-weight: 800;
  letter-spacing: .1em;
  color: #bfcd7e;
  font-family: 'Open Sans', sans-serif;
}

.user-location {
  margin-left: 8px;
  font-family: 'Open Sans', sans-serif;
}

.user-registration-date {
  margin-bottom: 30px;
  font-family: 'Open Sans', sans-serif;
}

.alerts-leave {
  opacity: 1;
  padding-bottom: 15px;
  padding-top: 15px;
  margin-bottom: 20px;
  height: 50px;
}
.alerts-leave.alerts-leave-active {
  overflow: hidden;
  opacity: 0.01;
  padding-top: 0px;
  padding-bottom: 0px;
  margin-bottom: 0px;
  height: 0px;
  transition: opacity 300ms ease-in,
              padding-top 500ms ease-in,
              padding-bottom 500ms ease-in,
              margin-bottom 500ms ease-in,
              height 500ms ease-in;
}

